.container {
    display: flex;
    flex-flow: row;
}

.imglogin {
    width: 50%
}

.loginimg {
    width: 100%;
    height: 100%;
}

.signin {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 50%;
    margin-top: 150px;
}

.logo {
    flex: 1 1 auto;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 35%;
    height: auto;
    margin: 20% auto 0 auto;
}

input {
    width: 250px;
    height: 35px;
    margin: 50px auto auto auto;
    background-color: transparent;
    color: black;
    font-size: 20px;
    border-radius: 20px;
    border: 2px solid #FCBF49;
    text-align: center;
}

.buttonlink {
    width: 180px;
    height: 37px;
    margin: 30px auto 30px auto;
    background-color: #FCBF49;
    color: black;
    font-size: 20px;
    font-weight: 500;
    border-radius: 20px;
    border: none;
    text-align: center;
    padding: 2px auto 2px auto;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
}

a:link, a:visited, a:active {
    text-decoration: none;
    color: black;
    font-size: 20px;
}